<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Register</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
        <ion-input placeholder="Enter username" @ionChange="validate" v-model="username"></ion-input>
        <ion-input placeholder="Enter password" @ionChange="validate" v-model="password" type="password"></ion-input>
        <ion-input placeholder="Repeat password" @ionChange="validate" v-model="repeatPassword" type="password"></ion-input>
        <ion-input placeholder="Enter email" @ionChange="validate" v-model="email"></ion-input>
        <ion-button :disabled="disabled" expand="full" @click="register">Register</ion-button>
      </ion-content>
  </ion-page>
</template>
<script>
import { IonContent, IonInput, IonButton, toastController, IonPage, IonToolbar, IonHeader, IonTitle } from '@ionic/vue';
import { warning } from 'ionicons/icons';
import { defineComponent } from 'vue';
 import { useRouter } from 'vue-router';
import axios from 'axios';

export default defineComponent({
  components: { IonInput, IonButton, IonContent, IonPage, IonToolbar, IonHeader, IonTitle },
  setup() {
    const router = useRouter();
    return { warning, router };
  },
  methods: {
     async failedRegisterToast() {
        const toast = await toastController
          .create({
            message: 'Your registration data was incorrent. Try again.',
            duration: 2000
          })
        return toast.present();
      },
      validate(){
        if (this.username != "" && this.password != "" && this.repeatPassword != "" && this.repeatPassword == this.password && this.email != ""){
          this.disabled = false;
        }else{
          this.disabled = true;
        }
      },
      async register(){
        const payload = {
            username: this.username,
            email: this.email,
            password: this.password,
        };

        axios.post(process.env.VUE_APP_TEACHER_API_URL+'/teachermanagement/teachers', payload).then(() => {
          this.username = "";
          this.password = "";
          this.repeatPassword = "";
          this.email = "";
          this.disabled = true;
          this.router.push("/");
        }).catch((error) => {
          console.error(error)
          this.failedRegisterToast();
        });
      }
  },
  data: () => ({
    username: "",
    password: "",
    repeatPassword: "",
    email: "",
    disabled: true
  }),
});
</script>

